@use "./modules/" as m;

.content__cols {
  display: grid;
  grid-template-columns: 1fr 1fr;
  min-height: 100vh;

  @media only screen and (width < 50rem) {
    grid-template-columns: 1fr;
  }
}

.section {
  padding: 5rem;

  @media only screen and (width < 50rem) {
    padding: 1rem;
  }

  #{&}__star {
    font-size: var(--fs-1);
    margin-bottom: 1rem;
    display: block;

    @media only screen and (width < 50rem) {
      margin-bottom: 1.5rem;
    }
  }

  &__heading {
    font-size: var(--fs-3);
    margin-bottom: 0.5rem;
    color: m.color(primary__black);
  }

  &__text {
    color: m.color(neutral__dark-grey);
    font-size: var(--fs-0);
  }

  &__list {
    margin-top: 3.5rem;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    padding: 0;
  }

  &__list-item {
    font-size: var(--fs--1);
    display: flex;
    gap: 1.5rem;
    align-items: start;
    justify-content: start;
    list-style: none;
    // list-style-image: url("../assets/images/Done_ring_round_fill.svg");

    &::before {
      content: url("../assets/images/Done_ring_round_fill.svg");
      width: 0.25rem;
      font-size: 0.25rem;
      display: block;
    }
  }
}

// testimonials
.testimonials {
  display: flex;
  flex-direction: column;
  //   align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 1rem;
  gap: 3rem;

  .google {
    margin-left: 5rem;

    @media only screen and (width < 50rem) {
      margin-left: 2rem;
    }
  }

  &__item {
    max-width: 30rem;
    background-color: m.color(neutral__white);
    box-shadow: 0.125rem 0.125rem 2rem m.color(neutral__light-grey);
    border-radius: 1rem;
    padding: 2.5rem;

    &__company {
      max-width: 5rem;
    }

    &__header {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    .stars {
      display: flex;
      align-items: center;
    }

    &__name {
      font-size: var(--fs-0);
      margin-top: 1rem;
      margin-bottom: 0.5rem;
      color: m.color(primary__black);
    }

    &__text {
      font-size: var(--fs--1);
    }
  }
}
